// tooltip
// tooltip
.egs-tooltip {
  margin: 0;
  cursor: pointer;
  &__modal-in-title {
    position: absolute;
    padding: 5px;
    opacity: 0;
    visibility: hidden;
    z-index: 40;
    width: 300px;
    height: 100px;
    width: max-content;
    max-width: 260px;
    height: max-content; 
    background: #ffffff;
    border: 1px solid #9d9d9d;
    border-radius: 10px;
    margin-top: 10px;
    top: 100%;
    left: 50%;
    transition: all .3s;
    transform: translate(-50%, 0);
    text-align: left;

  }
  &__modal-in-product {
    position: absolute;
    padding: 5px;
    opacity: 0;
    visibility: hidden;
    z-index: 40;
    width: max-content;
    max-width: 200px;
    background: #ffffff;
    border: 1px solid #9d9d9d;
    border-radius: 10px;
    margin-top: 10px;
    top: 100%;
    left: 50%;
    transition: all .3s;
    transform: translate(-50%, 0);
    text-align: left;
  }
  &__text {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: $base-c;
  }
  // &__tooltip:before {
  //   content: "";
  //   z-index: 30;
  //   width: 0; 
  //   height: 0;
  //   position: absolute; 
  //   top: 100%; 
  //   // top: calc(50% - 12px);
  //   border-right: 12px solid transparent;
  //   border-top: 24px solid #ffffff;
  //   border-left: 12px solid transparent;
  // }
  .egs-tooltip__img:hover + .egs-tooltip__modal-in-product {
    font-size: 14px;
  }
  .egs-tooltip__img:hover + .egs-tooltip__modal-in-product {
    opacity: 1;
    visibility: visible;
  }
  .egs-tooltip__img:hover + .egs-tooltip__modal-in-title {
    font-size: 14px;
  }
  .egs-tooltip__img:hover + .egs-tooltip__modal-in-title {
    opacity: 1;
    visibility: visible;
  }

  
  &__big {
    width: 27px;
    height: 27px;
  }
}

